















export default {
	data() {
		return {
			data: {}
		};
	},
	onLoad:function(e){
		uni.showLoading({
			title: '加载中...',
		});
		uni.request({
			url: 'https://news-at.zhihu.com/api/4/news/'+e.id,
			method: 'GET',
			success: res => {
				console.log(res);
				this.data = res.data;
				this.data.body = res.data.body.replace(/“/g,'"').replace(/”/g,'"');
				uni.hideLoading();
				
			}
		});
	}
}



